
class RunConfig extends Phaser.Sprite {

    constructor(game, x, y, key, context, timedefault) {
      
        super(game, x, y);
        
        this.center = {x: x, y: y};
        
        this.game = game;
        
        var bStyle = {font: "140px Arial", fill: "#000000"};
        
        var text = null;
        
        this.timeup = timedefault;
        
        //
        
        this.xposOrig = 26;
        this.xpos = this.xposOrig;
        
        var that = this;
        
        var button10 = game.add.button(this.xpos, this.center.y*1.88, key, function() { that.timeup-=1; that.changeTimeText(); }, context, 0,1,2,3);
    		button10.anchor.x = 0.5; 
        button10.anchor.y = 0.5;
        button10.scale.setTo(0.14,0.14);
    		text = game.add.text(-30, -80, "-", bStyle);
    		button10.addChild(text);
        this.xpos+=38;
        
        var button11 = game.add.button(this.xpos, this.center.y*1.88, key, function() { that.timeup+=1; that.changeTimeText(); }, context, 0,1,2,3);
    		button11.anchor.x = 0.5; 
        button11.anchor.y = 0.5;
        button11.scale.setTo(0.14,0.14);
    		text = game.add.text(-30, -80, "+", bStyle);
    		button11.addChild(text);
        this.xpos+=44;
        
        this.changeTimeText();
        
    }
    
    changeTimeText() {
      console.log('changeTimeText '+this.timeup);
      if (this.timetext != null) {
        this.timetext.destroy();
      }
      this.timetext = this.game.add.text(this.xpos, this.center.y*1.884, this.timeup, 
      {font: "32px Optima"});
      this.timetext.anchor.set(0.5);
    }
    
    setTimeText(number) {
      this.timeup = number / 1000;
      this.changeTimeText();
    }
    
    getTimeText() {
      return this.timeup * 1000;
    }
    
    reset() {
      this.timeup = timedefault;
      this.changeTimeText();
    }

}

export default RunConfig;
