import ConfigQues from 'configQues';

class ConfigQuesImag extends ConfigQues {
  
  constructor(id, name, config, ante, afte, questions, wackys, sheetfile) {
    super(id, name, config, ante, afte, questions, wackys);
    this.sheetfile = sheetfile;
    //
    this.game = null;
    this.sprite = null;
  }
  
  pre(game,center) {
		console.log("pre");
		this.game = game;
		var loader = new Phaser.Loader(game);
		loader.spritesheet('sheet', this.sheetfile, 172, 160);
		loader.onLoadComplete.addOnce(this.onLoaded);
		loader.start();
	}
  
  onLoaded() {
		console.log('image sheet is loaded and ready to be used'); 
	}
  
  before(game,center) {
    if (this.sprite != null) {
      this.sprite.destroy();
    }
    this.sprite = this.game.add.sprite(center.x*0.77, center.y*0.19, 'sheet');
    this.sprite.scale.setTo(center.x*0.0028,center.y*0.0030);
  }
  
  after(game,center) {
		this.sprite.frame = this.index;
  }
  
}

export default ConfigQuesImag;
