
class Config {
  
  constructor(id, name, config) {
    this.id = id;
    this.name = name;
    this.timeup = 7000;
  	this.quizSize = 180;
  	this.choiceSize = 10;
    this.moveMainBy = 0;
  	this.timein = 0;
    this.setConfig(config);
  }
  
  setConfig(config) {
    for (var key in config) {
        if (config.hasOwnProperty(key)) {
            this[key] = config[key];
            // console.log(key + " -> " + p[key]);
        }
    }
  }

  pre(game, center) {}

  before(game, center) {}
  
  calculator() {
    return [-1, -1, -1];
  }
  
  getRandom(number) {
    return -1;
  }
  
  after(game, center) {}
  
  // get area() { return this.height * this.width; }
  // set area(value) { this.area = value; }

}

export default Config;
